//------------------------------------------------------------
// Step2
//------------------------------------------------------------

//=========================
// 入力画面
//=========================

// 基本的に既存のStep2を踏襲してます（css以外にhtmlやjsも）。その関係で書き方が理解し難いのがあります。ご了承ください。

// 大枠
//------------------------------------------------------------

.assessment-img {
	text-align: center;
}

.assessment-page-ttl {
	padding: vw(5) vw(10);
	background-color: colors(ttl);
	color: colors(col-white);
	font-size: vw(20);
	font-weight: 700;
	text-align: center;
}

// inputなどのパーツ

#slider {
	select,
	input[type="text"],
	input[type="password"],
	input[type="email"],
	input[type="tel"],
	input[type="readonly"],
	textarea {
		width: 100%;
	}
	.mileage-select {
		width: 85%;
	}
	label {
		display: block;
	}
}

// 入力ステップ
//------------------------------------------------------------

.assessment-form {
	&-step {
		display: table;
		position: relative;
		width: 100%;
		background-color: colors(col-xlightgray);
		text-align: center;
		&-list {
			@include table-cell;
			position: relative;
			height: vw(30);
			padding-right: vw(6);
			font-size: vw(14);
			font-weight: 700;
			line-height: 1;
			vertical-align: middle;
			&:after {
				position: absolute;
				top: 50%;
				right: vw(-6);
				border: vw(6) solid transparent;
				border-left: vw(6) solid colors(col-black);
				@include translate(0%, -50%);
				content: "";
			}
			&:last-child {
				padding-right: 0;
				&:after {
					border-width: 0;
				}
			}
			&.active {
				color: colors(col-blue);
			}
		}
	}
}

// フォーム内
//------------------------------------------------------------

// 見出し

.ttl-column {
	padding: vw(5);
	background-color: colors(col-blue);
	color: colors(col-white);
	font-size: vw(16);
	font-weight: 700;
}

// 項目

.assess-item {
	&-block {
		display: table;
		width: 100%;
		padding: vw(10) vw(5);
		box-sizing: border-box;
		table-layout: fixed;
	}
	&-ttl {
		@include table-cell;
		width: vw(110);
		font-size: vw(14);
		vertical-align: top;
	}
	&-body {
		@include table-cell;
		font-size: vw(14);
		vertical-align: top;
	}
	&-name {
		@include inline-block;
		width: vw(70);
		font-weight: 700;
	}
}

// 必須やOKなどのアイコン

.ico {
	&-label {
		@include inline-block;
		width: vw(24);
		height: vw(20);
		@include margin(null vw(4) null null);
		@include padding(null vw(4) null vw(4));
		border: vw(1) solid colors(col-lightgray);
		border-radius: vw(6);
		background-color: colors(col-white);
		font-size: vw(12);
		font-weight: normal;
		line-height: vw(20);
		text-align: center;
		vertical-align: top;
	}
	&-required {
		color: colors(col-red);
		border: vw(1) solid colors(col-red);
	}
	&-optional {
		color: colors(col-blue);
		border: vw(1) solid colors(col-blue);
	}
	&-okay {
		color: colors(col-blue);
		border: vw(1) solid colors(col-blue);
	}
}

// 確認/戻る/申し込むボタン

.pager {
	display: table;
	width: 100%;
	@include padding(vw(10) vw(10) vw(40) vw(10));
	box-sizing: border-box;
	table-layout: fixed;
	.prev {
		@include table-cell;
		width: 30%;
		vertical-align: top;
		a {
			color: colors(basetxt);
			text-decoration: none;
		}
		.slide1 & {
			width: 0;
		}
	}
	.next {
		@include table-cell;
		vertical-align: top;
		.slide1 & {
			width: 100%;
		}
	}
	.btn-back {
		font-weight: 700;
		text-align: center;
		vertical-align: middle;
	}
}

// 入力サポートふきだし
//------------------------------------------------------------

.input-assistant-box {
	position: relative;
}

.assistant-popup {
	&-box {
		position: absolute;
		display: none;
		bottom: vw(-40);
		right: vw(5);
		z-index: 10;
	}
	&-balloon {
		@include inline-block;
		position: relative;
		@include padding(vw(6) vw(10) vw(6) vw(6));
		border: vw(4) solid colors(col-orange);
		border-radius: vw(2);
		background: colors(col-gray);
		color: colors(col-white);
		font-size: vw(16);
		line-height: vw(22);
		vertical-align: bottom;
		@include box-shadow(vw(2),vw(2),vw(3),0px,rgba(colors(col-black),0.45));
		&:before {
			position: absolute;
			top: vw(-20);
			left: 50%;
			border: vw(6) solid transparent;
			border-bottom: vw(10) solid colors(col-orange);
			@include translate(-50%, 0);
			content: "";
		}
	}
	&-icon {
		width: vw(24);
		height: vw(24);
		margin-right: vw(4);
		vertical-align: inherit;
	}
}

//=========================
// 確認画面
//=========================

// 項目

.confirm-item {
	&-block {
		@extend .assess-item-block;
		padding: vw(10);
	}
	&-ttl {
		@extend .assess-item-ttl;
	}
	&-body {
		@extend .assess-item-body;
	}
	&-name {
		@extend .assess-item-name;
		width: auto;
	}
}

// 査定会社の選択
//------------------------------------------------------------

.attention {
	padding: vw(10);
	font-size: vw(14);
}

.company-select-box {
	margin: vw(20) 0 0;
}

.company {
	&-list {
		display: table;
		width: 100%;
		@include margin(vw(10) null vw(10) null);
		table-layout: fixed;
		&:first-child {
			margin-top: 0;
		}
		img {
			width: vw(88);
			height: vw(31);
		}
	}
	&-logo {
		@include table-cell;
		width: vw(90);
		vertical-align: top;
	}
	&-name {
		@include table-cell;
		@include padding(null vw(10) null vw(10));
		vertical-align: top;
	}
}

// 利用上の注意
//------------------------------------------------------------

.notes-area {
	padding: vw(10);
	border-top: vw(1) solid colors(col-xlightgray);
	background-color: colors(col-white);
	color: colors(basetxt);
	font-size: vw(12);
	line-height: 1.5;
}

//=========================
// 完了画面
//=========================

.thanks-page {
	font-size: vw(14);
}

// 相場表示
//------------------------------------------------------------

.view-assessment-area {
	@include margin(vw(20) null vw(20) null);
	padding: vw(10);
	border: vw(1) solid colors(disable);
	background-color: colors(col-lightyellow)
}

.user-car-info {
	&-block {
		clear: both;
	}
	&-ttl {
		font-size: vw(14);
		font-weight: 700;
	}
	&-tbl {
		width: 100%;
		border-top: vw(1) solid colors(col-blue);
		border-left: vw(1) solid colors(col-blue);
		background-color: colors(col-white);
		font-size: vw(12);
		th, td {
			padding: vw(3) vw(6);
			border-right: vw(1) solid colors(col-blue);
			border-bottom: vw(1) solid colors(col-blue);
		}
		th {
			width: vw(100);
			background-color: colors(col-blue);
			color: colors(col-white);
		}
		tr:not(:last-child) th {
			border-bottom: vw(1) solid colors(col-white);
		}
		td {
			background-color: colors(col-white);
		}
	}
}

.user-view-asssessment {
	&-block {
		clear: both;
		@include margin(null null vw(20) null);
	}
	&-ttl {
		font-size: vw(20);
		font-weight: 700;
		text-align: center;
		text-shadow: vw(1) vw(1) 0 rgba(colors(col-white), 1), vw(2) vw(2) vw(2) rgba(colors(col-black), 0.2);
	}
	&-supplytxt {
		@include margin(null null vw(10) null);
		font-size: vw(10);
	}
}

.assessment-price {
	&-box {
		@include margin(null null vw(10) null);
		padding: vw(5);
		border: vw(1) solid colors(col-blue);
		background: colors(col-white);
		font-size: vw(22);
		font-weight: 700;
		text-align: center;
	}
	&-txt {
		@include margin(null null vw(10) null);
		font-size: vw(14);
		font-weight: 700;
		text-align: center;
		text-shadow: vw(1) vw(1) 0 rgba(colors(col-white), 1), vw(2) vw(2) vw(2) rgba(colors(col-black), 0.2);
	}
}

.price-strong {
	color: colors(col-red);
	font-size: vw(36);
}

// サンクス画面で使われるテキスト
//------------------------------------------------------------

.thanks-fixed-txt {
	font-size: vw(12);
	&-ttl {
		color: colors(ttl);
		font-weight: 700;
		&:before {
			content: "■";
		}
	}
	&-body {
		@include margin(null null 1em 1em);
	}
}

// サンクスページのPR
//------------------------------------------------------------

.thanks-pr {
	&-area {
		@include margin(null null vw(20) null);
		padding: vw(10);
		background-color: colors(col-lightyellow);
		text-align: center;
		line-height: 1.5;
	}
	&-ttl {
		text-align: left;
	}
	&-body:not(:last-child) {
		@include margin(null null vw(20) null);
	}
}

// サイト回遊バナー
//------------------------------------------------------------

.mysite-bnr {
	&-area {
		text-align: center;
		a {
			text-decoration:  none;
		}
		figure {
			@include margin(vw(10) null vw(10) null);
			figcaption {
				color: colors(ttl);
			}
		}
	}
}

// 矢印付きブロックリンク
//------------------------------------------------------------

.text-with-arw-list {
	border: vw(1) solid colors(link);
	border-bottom: none;
	&:last-child {
		border-bottom: vw(1) solid colors(link);
	}
	a {
		display: block;
		padding: vw(7) vw(20) vw(7) vw(5);
		text-decoration: none;
		box-sizing: border-box;
		&:visited .ico-arrow {
			color: colors(visited);
		}
	}
	figure {
		@include flex-block;
		position: relative;
		-webkit-align-items: center;
		align-items: center;
		width: 100%;
		.ico-arrow {
			position: absolute;
			top: 50%;
			right: vw(-15);
			color: colors(link);
			@include translate(0, -50%);
		}
	}
}

//------------------------------------------------------------
// 事故車Step2
//------------------------------------------------------------

.jiko {
	#slider {
		div {
			@include padding(null vw(10) null vw(10));
			font-size: vw(14);
			&.pager {
				font-size: vw(16);
			}
			&.notes-area {
				font-size: vw(12);
				border: vw(1) solid colors(col-xlightgray);
			}
		}
		select,
		input[type="text"],
		input[type="password"],
		input[type="email"],
		input[type="tel"],
		input[type="readonly"],
		textarea {
			width: 95%;
			&#ddlCYear {
				width: vw(150);
			}
			&#ddlCMonth {
				width: vw(60);
			}
		}
		label {
			&#lblFumei,&#lblDisplacement {
				@include inline-block;
				vertical-align: top;
			}
		}
		.link {
			color: colors(link);
			text-decoration: underline;
		}
		.clearfix .link {
			float: right;
		}
		.check img {
			width: vw(20);
			height: vw(17);
			vertical-align: middle;
		}
	}
}

#pagestep {
	position: relative;
	width: 100%;
	text-align: center;
	span {
		@include inline-block;
		width: vw(8);
		height: vw(8);
		@include margin(null vw(5) null vw(5));
		@include border-radius(50%,50%,50%,50%);
		background-color: colors(col-gray);
		&.active {
			background-color: colors(col-blue);
		}
	}
	&:before,&:after {
		position: absolute;
		top: 50%;
		width: vw(5);
		height: vw(5);
		border: 2px solid colors(col-gray);
		transform: rotate(45deg) translateY(-50%);
		content: "";
	}
	&:before {
		left: vw(10);
		border-right: none;
		border-top: none;
	}
	&:after {
		right: vw(10);
		border-left: none;
		border-bottom: none;
	}
}

// 説明ポップアップ
//------------------------------------------------------------

.pWindow {
	@include padding(null vw(10) null vw(10));
	font-size: vw(10);
	box-sizing: border-box;
	.closeBtn {
		@include inline-block;
		position: absolute;
		top: vw(-5);
		right: vw(3);
		width: vw(20);
		height: vw(20);
		@include border-radius(50%,50%,50%,50%);
		border: vw(1) solid colors(col-white);
		background-color: colors(link);
		color: colors(col-white);
		font-size: 0;
		font-weight: 700;
		line-height: vw(20);
		text-align: center;
		text-decoration: none;
		vertical-align: middle;
		&:before,&:after {
			position: absolute;
			top: 50%;
			left: 50%;
			width: calc(100% - #{vw(6)});
			height: vw(2);
			background: colors(col-white);
			content: "";
		}
		&:before {
			transform: translateX(-50%) translateY(-50%) rotate(45deg);
		}
		&:after {
			transform: translateX(-50%) translateY(-50%) rotate(-45deg);
		}
	}
	.pwContainer {
		border: vw(1) solid colors(col-lightgray);
		background-color: colors(col-white);
		@include box-shadow(0px,0px,vw(3),0,rgba(colors(col-black), 0.5));
	}
	.titleBar {
		background-color: colors(col-blue);
		color: colors(col-white);
		font-weight: 700;
		.pwBarCenter {
			@include padding(vw(3) vw(20) vw(3) vw(3));
		}
	}
	.contentsBody {
		padding: vw(3);
	}
}

//------------------------------------------------------------
// 商用車Step2
//------------------------------------------------------------

//=========================
// 初めのページ
//=========================

.commercial-car-type {
	&-area {
		margin-bottom: vw(20);
		li {
			border: vw(1) solid colors(link);
			border-bottom: none;
			&:last-child {
				border-bottom: vw(1) solid colors(link);
			}
			a {
				&:link {
					display: block;
					position: relative;
					@include padding(vw(10) vw(20) vw(10) vw(10));
					text-decoration: none;
					&:after {
						position: absolute;
						top: 50%;
						right: vw(10);
						width: vw(4);
						height: vw(4);
						border-right: vw(2) solid colors(link);
						border-bottom: vw(2) solid colors(link);
						transform: translateY(-50%) rotate(-45deg);
						content: "";
					}
				}
				&:visited {
					border-right: vw(2) solid colors(visited);
					border-bottom: vw(2) solid colors(visited);
				}
			}
		}
	}
}

.commercial-car-type-fumei {
	margin-bottom: vw(20);
	padding: vw(10);
	font-size: vw(14);
}

//=========================
// 入力ページ以降
//=========================

.commercial-car {
	div {
		@include margin(vw(10) null vw(10) null);
		@include padding(null vw(10) null vw(10));
		font-size: vw(14);
	}
	select,
	input[type="text"],
	input[type="password"],
	input[type="email"],
	input[type="tel"],
	input[type="readonly"],
	textarea {
		width: 90%;
		@include margin(vw(3) null vw(3) null);
	}
	.check img {
		width: vw(20);
		height: vw(17);
		vertical-align: middle;
	}
	.BtnArea {
		text-align: center;
		center {
			font-size: vw(12);
		}
	}
	.StepCap {
		margin-top: vw(40);
		font-size: vw(12);
		border: vw(1) solid colors(col-xlightgray);
	}
}

//------------------------------------------------------------
// お問い合わせ
//------------------------------------------------------------

.contact-caution-area {
	margin-bottom: vw(20);
	padding: vw(10);
	background-color: colors(col-lightyellow);
	font-size: vw(12);
	&-inner {
		height: vw(120);
		overflow: auto;
		&::-webkit-scrollbar {
			width: vw(4);
			border-radius: vw(2);
			&-track {
				border-radius: vw(2);
				background-color: rgba(colors(col-xlightgray), 0.75);
			}
			&-thumb {
				border-radius: vw(2);
				background-color: rgba(colors(col-gray), 0.75);
			}
		}
	}
}

.contact-tbl {
	display: block;
	width: 100%;
	margin-bottom: vw(20);
	tr {
		display: block;
		@include padding(vw(10) null vw(10) null);
		border-bottom: vw(1) dotted colors(col-lightgray);
	}
	th, td {
		box-sizing: border-box;
	}
	th {
		display: block;
		text-align: left;
	}
	td {
		display: block;
	}
	tbody {
		display: block;
		tr {
			&:last-child {
				padding-bottom: 0;
				border-bottom: none;
			}
		}
	}
	select,
	input[type="text"],
	input[type="password"],
	input[type="email"],
	input[type="tel"],
	input[type="readonly"],
	textarea {
		width: 100%;
	}
	textarea {
		height: vw(100);
	}
}

//------------------------------------------------------------
// 査定キャンセル
//------------------------------------------------------------

.cancel-tbl {
	display: block;
	width: 100%;
	margin-bottom: vw(20);
	tr {
		display: block;
		@include padding(vw(10) null vw(10) null);
		border-bottom: vw(1) dotted colors(col-lightgray);
	}
	th, td {
		box-sizing: border-box;
	}
	th {
		display: block;
		text-align: left;
	}
	td {
		display: block;
	}
	tbody {
		display: block;
		tr {
			&:last-child {
				padding-bottom: 0;
				border-bottom: none;
			}
		}
	}
	select,
	input[type="text"],
	input[type="password"],
	input[type="email"],
	input[type="tel"],
	input[type="readonly"],
	textarea {
		width: 100%;
	}
	textarea {
		height: vw(100);
	}
}

.news-cancel-area {
	margin-top: vw(30);
	padding-top: vw(20);
	border-top: vw(1) solid colors(col-gray);
}